import React, { useState } from "react";
import ReactDOM from "react-dom";
import EditableText from "./EditableText";
import { img } from "./landingImg.png";

import "./styles.css";

function useImageZoom(maxZoomLevel = 5) {
  const minZoomLevel = 1;

  const [zoomLevel, setZoomLevel] = React.useState(minZoomLevel);
  const [rotate, setRotate] = React.useState(false);

  function zoomIn() {
    setRotate(false);
    setZoomLevel((zoomLevel) =>
      zoomLevel < maxZoomLevel ? zoomLevel + 1 : zoomLevel
    );
  }

  function zoomOut() {
    setRotate(false);
    setZoomLevel((zoomLevel) =>
      zoomLevel > minZoomLevel ? zoomLevel - 1 : minZoomLevel
    );
  }

  function resetZoom() {
    setRotate(false);
    setZoomLevel(minZoomLevel);
  }

  function rotation() {
    setRotate(true);
  }

  const zoomStyles = {
    transform: rotate ? `rotate(90deg)` : `scale(${zoomLevel})`,
  };

  const handlers = {
    zoomIn,
    zoomOut,
    rotation,
    resetZoom,
  };

  return [zoomStyles, handlers];
}

function App() {
  const [zoomStyles, handlers] = useImageZoom();
  const [rotation, setRotation] = useState({
    rotation: 0,
  });

  return (
    <div className="App">
      <h1>Editable Text Demo</h1>
      <EditableText text="Kaung Myat Lwin" />
      <div className="buttons">
        <button onClick={handlers.zoomIn}>ZOOM IN</button>
        <button onClick={handlers.zoomOut}>ZOOM OUT</button>
      </div>
      <div className="preview">
        <img
          style={{ ...zoomStyles }}
          onClick={handlers.resetZoom}
          src={img}
          alt="preview box"
        />
        <input onClick={handlers.rotation} type="button" value="right" />
      </div>
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
